.flex-container{
 /*display: flex;     
 flex-direction: row;*/
 
}

.myImg {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 150px;
    height: 150px;
  }
  
  .myImg:hover {
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
  }

  .imageBox{
      border: 1px solid #cccccc;
      float: left;
      text-align: center;
      padding: 1em;
      margin: 1em;
  }

  .imageBox h3{
      margin: 0;
  }

  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 25%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    padding: 2em;
  }
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  .inImg{
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 150px;
    height: 150px;
    display: block;
    margin: 0 auto;
  }
  .updateForm label,input{
    display: inline-block;
  }
  .updateForm label {
    width: 18%;
    text-align: right;
    margin: 0.5em;
  }
  
  .updateForm label + input {
    width: 50%;
    margin: 0 10% 0 4%;
  }
  
   .updateForm input + input {
    float: right;
  }

  .exitbtn{
    background: url(../images/close.png);
    background-size: 30px;
    background-repeat: no-repeat;
    border: none;
    width: 30px;
    height: 30px;
    outline: none;
    float: right;
    cursor: pointer;
  }
.header{
  background: dodgerblue;
  padding: 1em;
}

.header ul{
  margin:0;
}
.header ul li{
  display: inline;
  margin: 1em;
}
.header ul li a{
  color: #ffffff;
  font-weight: bold;
  text-decoration: none;
}

.Login{
  border: 1px solid #cccccc;
}

.Login input[type=text]{
  width: 70%;
  padding: 0.5em;
  border-radius: 7px;
}
.Login input[type=password]{
  width: 70%;
  padding: 0.5em;
  border-radius: 7px;
}

.Login input{
  display: block;
  margin: 1em;
}

.Login input[type="submit"] {
	background: dodgerblue;
	border: none;
	padding: 0.5em;
	color: #FFF;
	font-weight: bold;
	cursor: pointer;
}

.LoginForm{
  width: 20%;
  margin: 0 auto;
}

.errorMessage {
	background: orangered;
	color: #FFFFFF;
	padding: 1em;
	border-radius: 10px;
	margin: 1em;
}

.gHead h2
{
  padding: 1em;
  color: olive;
}